*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: inherit; }

body {
  box-sizing: border-box; }

::selection {
  background-color: #33b433;
  color: #fff; }

.header {
  background-image: linear-gradient(to right bottom, #33b433, #73866b);
  color: #fff;
  font-family: "Pragati Narrow", sans-serif;
  display: grid;
  grid-template-columns: 1fr; }
  .header__title {
    display: grid;
    justify-content: center;
    grid-template-columns: 1fr;
    grid-template-rows: repeat(2, min-content);
    justify-items: center; }
    .header__title-1 {
      font-size: 2.7em;
      text-transform: uppercase; }
      @media only screen and (max-width: 62.5em) {
        .header__title-1 {
          font-size: 2.43em; } }
      @media only screen and (max-width: 29.37em) {
        .header__title-1 {
          font-size: 1.875em; } }
    .header__title-2 {
      font-family: "Dancing Script", cursive;
      font-size: 2em;
      align-self: center; }

.navbar {
  margin-top: 1rem;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: min-content;
  grid-column-gap: 2rem; }
  .navbar__item-1 {
    justify-self: flex-end;
    text-decoration: none;
    font-size: 1.625em;
    font-family: inherit;
    color: #295337;
    font-weight: 600;
    transition: all .2s; }
    @media only screen and (max-width: 31.25em) {
      .navbar__item-1 {
        font-size: 1.50em; } }
    .navbar__item-1:hover {
      color: #397a4f;
      transition: all .2s; }
  .navbar__item-2 {
    text-decoration: none;
    font-size: 1.625em;
    font-family: inherit;
    color: #295337;
    font-weight: 600;
    transition: all .2s; }
    @media only screen and (max-width: 31.25em) {
      .navbar__item-2 {
        font-size: 1.50em; } }
    .navbar__item-2:hover {
      color: #397a4f;
      transition: all .2s; }

.content {
  background-color: #44725b;
  color: #fff;
  display: grid;
  grid-template-columns: minmax(10px, 200px) 1fr minmax(10px, 200px);
  grid-template-rows: repeat(auto-fit, min-content);
  font-family: "Pragati Narrow", sans-serif;
  padding: 2rem; }
  .content-1 {
    grid-column: 2 / 3;
    display: grid; }
    @media only screen and (max-width: 75em) {
      .content-1 {
        grid-column: 1 / -1; } }
  .content__img {
    width: 11rem;
    height: 12rem;
    border-radius: 50%;
    justify-self: center;
    margin-bottom: 1rem; }
    @media only screen and (max-width: 62.5em) {
      .content__img {
        width: 7rem;
        height: 8rem; } }
  .content__title {
    font-family: inherit;
    text-transform: uppercase; }
    @media only screen and (max-width: 43.75em) {
      .content__title {
        font-size: 1.4em; } }
  .content__paragraph {
    font-size: 1.375em; }
    @media only screen and (max-width: 43.75em) {
      .content__paragraph {
        font-size: 1.255em; } }

.footer {
  background-color: #24503a;
  color: #bebcbc;
  padding: 2rem;
  text-align: center;
  font-family: "Pragati Narrow", sans-serif;
  font-size: 1.125em; }
  .footer__copyright {
    line-height: 3; }

.repo__container {
  font-family: "Pragati Narrow", sans-serif;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: repeat(5, min-content);
  grid-row-gap: 5px;
  justify-items: center;
  padding: 5px;
  margin-bottom: 2rem;
  background-color: #1f7e76;
  border-radius: 5px;
  border-bottom: 1rem solid rgba(255, 255, 255, 0.651);
  border-top: 1rem solid rgba(255, 255, 255, 0.651); }
  .repo__container-header {
    font-family: inherit;
    font-size: 1.35em;
    font-weight: 600; }
  .repo__container-link {
    text-decoration: none;
    color: #a0adb3;
    transition: all .3s;
    letter-spacing: 3px; }
    .repo__container-link:hover {
      border-bottom: 2px solid currentColor;
      transition: all .3s; }

.projectName {
  font-size: 1.50em;
  margin-left: 5px;
  color: #05fd81; }
  @media only screen and (max-width: 43.75em) {
    .projectName {
      font-size: 1.30em; } }
  @media only screen and (max-width: 31.25em) {
    .projectName {
      font-size: 1.125em; } }

.u-margin-top-2 {
  margin-top: 2rem; }

.social-media {
  padding: 1.2rem 0;
  display: grid;
  grid-template-columns: repeat(5, min-content);
  grid-column-gap: 1rem; }
  .social-media-icon {
    width: 70px;
    height: 70px; }
    @media only screen and (max-width: 43.75em) {
      .social-media-icon {
        width: 55px;
        height: 55px; } }
    @media only screen and (max-width: 31.25em) {
      .social-media-icon {
        width: 45px;
        height: 45px; } }

.contact-me {
  justify-items: center; }

hr {
  height: 5px;
  background-color: #fff; }

.loading-box {
  width: auto;
  background-color: #2d2da3;
  color: #fff;
  font-size: 1.55em;
  padding: 5px;
  margin-top: 1rem; }
